import { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify';
import { createTitle, updateTitle, getTitle } from '../../services/TitleService'
import { MDBInput, MDBBtn, MDBTextArea } from 'mdb-react-ui-kit';
import { Select } from 'react-select';
import Async, { useAsync } from 'react-select/async';

const initialState = {
    id: "",
    first_variable: "",
    second_variable: "",
    connector: "",
    population: "",
    location: "",
    timestamp: ""
};

const ProjectForm = () => {
    const [title, setTitle] = useState(initialState);
    const params = useParams();
    const navigate = useNavigate();

    const handleInputChange = ({ target: { name, value } }) => setTitle({ ...title, [name]: value });

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!params.id) {
            await createTitle(title);
              toast("Cambios guardados.", {
                type: "success",
              });
        } else {
            await updateTitle(title.id, title);
              toast("Cambios guardados.", {
                type: "success",
              });
        }

        // Clean Form
        setTitle(initialState);
        navigate(`/detalle/${params.id}`);
    };

    const getTitleById = async (id) => {
        try {
            const querySnapshot = await getTitle(id);
            const docs = [];
            querySnapshot.forEach((doc) => {
                docs.push({ ...doc.data(), id: doc.id });
            });
            if(docs.length > 0) {
                const doc = docs[0];
                setTitle({ ...doc });
            }
        } catch (error) {
            console.error(error);
        }
    };

    useEffect(() => {
        if (params.id) {
            getTitleById(params.id);
        }
    }, [params.id]);

    return (
        <section class="mb-4">
            <form onSubmit={handleSubmit}>
            <div class="card">
                <div class="card-header">
                    <h5 class="mb-0"><strong>Título</strong></h5>
                </div>
                <div class="card-body">
                    <div class="form-outline mb-4">
                        <MDBInput label="Primera Variable" name="first_variable" type="text" onChange={handleInputChange} value={title.first_variable} />
                    </div>
                    
                    <div class="form-outline mb-4">
                        <MDBInput label="Segunda Variable" name="second_variable" onChange={handleInputChange} value={title.second_variable} />
                    </div>

                    <div class="form-outline mb-4">
                        <label>Conector</label>
                        <select class="form-select" name="connector" onChange={handleInputChange} value={title.connector}>
                            <option>y</option>
                            <option>para</option>
                            <option>contribución</option>
                            <option>impacto</option>
                            <option>efecto</option>
                            <option>influye</option>
                            <option>en la</option>
                            <option>durante</option>
                            <option>relación</option>
                            <option>con</option>
                        </select>
                    </div>

                    <div class="form-outline mb-4">
                        <MDBInput label="Población" name="population" onChange={handleInputChange} value={title.population} />
                    </div>

                    <div class="form-outline mb-4">
                        <MDBInput label="Lugar" name="location" onChange={handleInputChange} value={title.location} />
                    </div>

                    <div class="form-outline mb-4">
                        <MDBInput label="Tiempo" name="timestamp" onChange={handleInputChange} value={title.timestamp} />
                    </div>
                </div>
                <div class="card-footer d-flex justify-content-between">
                    <MDBBtn className="text-dark" color="light" type="button" onClick={() => navigate(`/detalle/${params.id}`)}>Regresar</MDBBtn>
                    <MDBBtn color="primary" disabled={!title.first_variable || !title.second_variable}>
                        Guardar
                    </MDBBtn>
                </div>
            </div>
            </form>
        </section>
    )
}

export default ProjectForm