import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { Login } from "./components/auth/Login";
import { Register } from './components/auth/Register';
import AppLayout from "./components/AppLayout";
import ProjectList from './components/projects/ProjectList';
import ProjectForm from './components/projects/ProjectForm';
import ProjectDetail from './components/projects/ProjectDetail';
import TitleEditor from './components/editors/TitleEditor';
import ContentEditor from './components/editors/ContentEditor';

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/ingresar" element={<Login />} />
        <Route path="/registro" element={<Register />} />
        <Route path="/" element={<AppLayout />}>
          <Route index element={<ProjectList />} />
          <Route path="crear" element={<ProjectForm />} />
          <Route path="editar/:id" element={<ProjectForm />} />
          <Route path="detalle/:id" element={<ProjectDetail />}/>
          <Route path="detalle/:id/titulo" element={<TitleEditor />} />
          <Route path="detalle/:id/contenido" element={<ContentEditor />} />
        </Route>
        <Route path="*" element={<h1>No Encontrado.</h1>} />
      </Routes>
    </Router>
  );
}

export default App;
