import { deleteProject } from "../../services/ProjectsService";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
import { MDBBtn } from "mdb-react-ui-kit";

const ProjectDetail = ({ project }) => {
    const navigate = useNavigate();
    const params = useParams();

    return (
        <>
            <div className="d-flex justify-content-between">
                <h1>Detalle de Proyecto</h1>
                <div className="d-flex align-items-center">
                    <MDBBtn color="light" onClick={() => navigate("/")}>
                        <i className='fas fa-arrow-left'></i>&emsp;Regresar
                    </MDBBtn>
                </div>
            </div>
            <div className="card mb-3" onClick={() => navigate(`/detalle/${params.id}/titulo`)}>
                <div className="card-body">
                    <div className="d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <i class="fas fa-3x fa-lightbulb"></i>&emsp;
                            <h2 className="my-0">Título</h2>
                        </div>
                    </div>
                </div>
            </div>
            <div className="card mb-3">
                <div className="card-body">
                    <div className="d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <i class="fas fa-3x fa-question"></i>&emsp;
                            <h2 className="my-0">Problema</h2>
                        </div>
                    </div>
                </div>
            </div>
            <div className="card mb-3">
                <div className="card-body">
                    <div className="d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <i class="fas fa-3x fa-bullseye"></i>&emsp;
                            <h2 className="my-0">Objetivos</h2>
                        </div>
                    </div>
                </div>
            </div>
            <div className="card mb-3" onClick={() => navigate(`/detalle/${params.id}/contenido`)}>
                <div className="card-body">
                    <div className="d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <i class="fas fa-3x fa-align-justify"></i>&emsp;
                            <h2 className="my-0">Contenido</h2>
                        </div>
                    </div>
                </div>
            </div>
            <div className="card mb-3">
                <div className="card-body">
                    <div className="d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <i class="fas fa-3x fa-book"></i>&emsp;
                            <h2 className="my-0">Referencias</h2>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default ProjectDetail