import { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify';
import { createProject, getProject, updateProject } from '../../services/ProjectsService'
import { MDBInput, MDBBtn, MDBTextArea } from 'mdb-react-ui-kit';
import { Select } from 'react-select';
import Async, { useAsync } from 'react-select/async';

const initialState = {
    name: "",
    career_id: "",
    study_center_id: "",
    user_id: "",
    description: ""
};

const ProjectForm = () => {
    const [project, setProject] = useState(initialState);
    const params = useParams();
    const navigate = useNavigate();

    const handleInputChange = ({ target: { name, value } }) => setProject({ ...project, [name]: value });

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!params.id) {
            await createProject(project);
              toast("Proyecto creado.", {
                type: "success",
              });
        } else {
            await updateProject(params.id, project);
              toast("Cambios guardados.", {
                type: "success",
              });
        }

        // Clean Form
        setProject(initialState);
        navigate("/");
    };

    const getProjectById = async (id) => {
        try {
            const doc = await getProject(id);
            setProject({ ...doc.data() });
        } catch (error) {
            console.error(error);
        }
    };

    useEffect(() => {
        if (params.id) {
            getProjectById(params.id);
        }
    }, [params.id]);

    return (
        <section class="mb-4">
            <form onSubmit={handleSubmit}>
            <div class="card">
                <div class="card-header">
                    <h5 class="mb-0"><strong>{!params.id ? "Nuevo" : "Editar"} Proyecto</strong></h5>
                </div>
                <div class="card-body">
                    <div class="form-outline mb-4">
                        <MDBInput label="Nombre" name="name" type="text" onChange={handleInputChange} value={project.name} />
                    </div>
                    
                    <div class="form-outline mb-4">
                        <MDBTextArea label="Descripción" name="description" onChange={handleInputChange} value={project.description} />
                    </div>

                    <div class="form-outline mb-4">
                        <label>Centro de Estudios</label>
                        <select class="form-select" name="study_center_id" onChange={handleInputChange} value={project.study_center_id}>
                            <option value="1">Universidad Mayor San Marcos</option>
                            <option value="2">Pontificia Universidad la Católica</option>
                            <option value="3">Universidad Nacional de Ingeniería</option>
                            <option value="4">Universidad Peruana de Ciencias Aplicadas</option>
                            <option value="5">Universidad de Lima</option>
                            <option value="6">Universidad Privada del Norte</option>
                            <option value="7">Universidad del Pacífico</option>
                            <option value="8">Universidad de Piura</option>
                        </select>
                    </div>

                    <div class="form-outline mb-4">
                        <label>Carrera</label>
                        <select class="form-select" name="career_id" onChange={handleInputChange} value={project.career_id}>
                            <option value="1">Medicina</option>
                            <option value="2">Derecho</option>
                            <option value="3">Ingeniería</option>
                            <option value="4">Periodismo</option>
                            <option value="5">Arquitectura</option>
                            <option value="6">Comunicaciones</option>
                            <option value="7">Educación</option>
                            <option value="8">Arte</option>
                        </select>
                    </div>

                </div>
                <div class="card-footer d-flex justify-content-between">
                    <MDBBtn className="text-dark" color="light" type="button" onClick={() => navigate("/")}>Regresar</MDBBtn>
                    <MDBBtn color="primary" disabled={!project.name || !project.user_id}>
                        {!params.id ? "Crear" : "Guardar"}
                    </MDBBtn>
                </div>
            </div>
            </form>
        </section>
    )
}

export default ProjectForm