import React from 'react'
import AppHeader from './AppHeader';
import AppMain from './AppMain';
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const AppLayout = () => {
    return (
        <>
            <AppHeader />
            <AppMain />
            <ToastContainer />
        </>
    )
}

export default AppLayout